  import mapboxgl from 'mapbox-gl';
  import '../lib/mapbox-gl/dist/mapbox-gl.css';
  import '../lib/mapbox-gl/dist/mapbox-gl';
  import {maplayer} from '../layer.js';
  import '../lib/animate.css';
  import {getconfig} from '../dataService/service';

  export default {
    data() {
      return {
        options1: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: '',
          disabled: true
        }, {
          value: '2',
          label: '',
          disabled: true
        }],
        value1: '',
        options2: [{
          value: '1',
          label: 'poi'
        }, {
          value: '2',
          label: 'poi_road',
          disabled: true
        }],
        value2: '',
        tableData: [{
          taskId: '2016',
          province: '',
          proApart: '1',
          program: 'idb_conv',
          stage: 'day',
          state: '',
          repeatTimes: '0',
          startTime: '2017-12-12 16:20:09',
          endTime: '2017-12-12 16:20:09',
          spendtime: '12.9',
          logfile: '11111'
        }, {
          taskId: '2016',
          province: '',
          proApart: '1',
          program: 'idb_conv',
          stage: 'day',
          state: '',
          repeatTimes: '0',
          startTime: '2017-12-12 16:20:09',
          endTime: '2017-12-12 16:20:09',
          spendtime: '12.9',
          logfile: '11111'
        }, {
          taskId: '2016',
          province: '',
          proApart: '1',
          program: 'idb_conv',
          stage: 'day',
          state: '',
          repeatTimes: '0',
          startTime: '2017-12-12 16:20:09',
          endTime: '2017-12-12 16:20:09',
          spendtime: '12.9',
          logfile: '11111'
        }, {
          taskId: '2016',
          province: '',
          proApart: '1',
          program: 'idb_conv',
          stage: 'day',
          state: '',
          repeatTimes: '0',
          startTime: '2017-12-12 16:20:09',
          endTime: '2017-12-12 16:20:09',
          spendtime: '12.9',
          logfile: '11111'
        }],
        show: false
      }
    },
    mounted: function () {
      this.createMap()
      this.initConfig()
    },
    methods: {
      createMap: function () {
        mapboxgl.mapboxToken = 'pk.eyJ1IjoiZmFuZ2xhbmsiLCJhIjoiY2lpcjc1YzQxMDA5NHZra3NpaDAyODB4eSJ9.z6uZHccXvtyVqA5zmalfGg',
          this.map = new mapboxgl.Map({
            container: 'map',
            style: maplayer.simple,
            zoom: 3.8,
            maxZoom: 4.5,
            minZoom: 3,
            center: [107.02932, 37.68486],
            repaint: true,
            pitch: 0
          })

        let that = this;                   //thisvue-VueCompents
        this.map.on('load', function () {
          that.map.addLayer({
            "id": "plate",
            "type": "fill",
            "source": {
              "type": "geojson",
              "data": {
                "type": "Feature",
                "properties": {},
                "geometry": {
                  "type": "Polygon",
                  "coordinates": [
                    [
                      [109.2041015625, 32.3057060139],
                      [110.4345703125, 34.2345123624],
                      [110.2807617188, 35.4248679193],
                      [109.7973632813, 37.3526928037],
                      [108.7646484375, 36.7564903295],
                      [108.5009765625, 35.7465122599],
                      [109.2041015625, 32.3057060139]
                    ]
                  ]
                }
              }
            },
            "layout": {
              "visibility": "visible"
            },
            "paint": {
              "fill-color": "rgba(255, 116, 116, .3)",
              'fill-outline-color': '#ff7474'
            }
          });

          that.map.on('click', 'plate', function () {
            that.show = true
          });
          this.popup = new mapboxgl.Popup({
            closeButton: false,
            closeOnClick: false
          });

          that.map.on('mouseenter', 'plate', function () {
            that.map.getCanvas().style.cursor = 'pointer';
            this.popup.setLngLat([109.55331540892581, 36.00854528903716])
              .setHTML('XXXX<br/>')
              .addTo(that.map);
          });

          that.map.on('mouseleave', 'plate', function () {
            that.map.getCanvas().style.cursor = '';
            this.popup.remove();
          });
        })
      },
      toLogin: function () {
        this.$router.push('/login');
      },
      //
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      //
      initConfig:function() {
        getconfig().then(function(data){
          console.log(data);

        })
      }


    }

  }


