          color: #ffffff;
          text-align: left;
          text-indent: 30px;
          border-top-left-radius: 6px;
          border-top-right-radius: 6px
        }
        ul {
          display: flex;
          justify-content: space-between;
          padding: 30px 40px 0px 40px;
          font-size: 14px;
          color: rgb(88, 150, 255);
          font-weight: 600;
        }
        .closeBtn {
          position: absolute;
          right: 20px;
          top: 17px;
          color: #ffffff;
          cursor: pointer;
          font-size: 21px;
          line-height: 1;
        }
      }
    }
  }

  .loginBtn {
    position: absolute;
    right: 10px;
    top: 10px;
    border-radius: 18px;
    background-color: #55c3fc;
    width: 130px;
    height: 35px;
    line-height: 35px;
    color: #ffffff;
    text-align: center;
    cursor: pointer;
  }

  .el-menu-item-group_title {
    padding: 0px;
    line-height: normal;
    font-size: 12px;
    color: #878d99;
  }

  .el-menu {
    border: none;
  }


</style>



